import {
  Container,
  Grid,
  Typography,
  Dialog,
  DialogTitle,
  DialogContentText,
  DialogContent,
  Theme,
} from '@mui/material';
import { makeStyles } from '@mui/styles';
import React, { useState, ComponentProps } from 'react';
import BenefitCard from 'components/Cards/BenefitCard';
import FadeUp from 'components/Animated/FadeUp';
import { useBooleanState } from '@lubycon/react';

interface MentoringBenefit {
  title: string;
  image: string;
  description: string;
}
const benefits: MentoringBenefit[] = [
  {
    title: 'Lean \n  ',
    image: 'https://assets.lubycon.io/illusts/chart.png',
    description: `Lean Thinking            .\n\n                ,    .          MVP             .\n\n  12  Lean          !`,
  },
  {
    title: '  \n  ',
    image: 'https://assets.lubycon.io/illusts/briefcase.png',
    description: `       ,     !\n\n     Chapter             , Guild   DevOps  ,            !`,
  },
  {
    title: '   \n ',
    image: 'https://assets.lubycon.io/illusts/networking.png',
    description: `          .        ,              .\n\n     ,  ,             .       .\n\n         ,     !`,
  },
  {
    title: '  \n  ',
    image: 'https://assets.lubycon.io/illusts/database.png',
    description: `       DevOps   ,           .\n\n     DevOps    DevOps        , CI/CD   .      SaaS   .\n\n          ,                ,  DevOps        !`,
  },
];

const useStyles = makeStyles<Theme>(({ spacing, typography, breakpoints, palette }) => ({
  root: {
    padding: `${spacing(34)}px 0`,
    zIndex: 1,
    [breakpoints.down('md')]: {
      padding: `${spacing(10)}px 0`,
    },
  },
  title: {
    fontWeight: typography.fontWeightBold,
    marginBottom: 82,
    fontSize: 56,
    [breakpoints.down('md')]: {
      fontSize: 24,
      marginBottom: 40,
    },
  },
  dialogRoot: {
    padding: spacing(5),
  },
  dialogImage: {
    width: 120,
    paddingTop: spacing(5),
    paddingLeft: spacing(2),
    margin: '0 auto',
  },
  dialogTitle: {
    textAlign: 'center',
    '& h2': {
      fontWeight: typography.fontWeightBold,
    },
  },
  dialogText: {
    whiteSpace: 'pre-line',
    color: palette.common.black,
    marginBottom: spacing(5),
  },
}));

const BenefitSection = () => {
  const [isOpenModal, openModal, closeModal] = useBooleanState();
  const [selectedModalData, setModalData] = useState<MentoringBenefit | null>(null);
  const styles = useStyles();
  return (
    <section className={styles.root}>
      <Container>
        <Typography component="h2" className={styles.title}>
            
        </Typography>
        <Grid container spacing={2}>
          {benefits.map(({ title, image, description }, index) => (
            <Grid item xs={12} sm={6} md={3} key={index}>
              <FadeUp>
                <BenefitCard
                  key={index}
                  title={title}
                  image={image}
                  onClick={() => {
                    openModal();
                    setModalData({ title, image, description });
                  }}
                />
              </FadeUp>
            </Grid>
          ))}
        </Grid>
      </Container>
      <BenefitDialog
        open={isOpenModal}
        onClose={() => {
          closeModal();
          setModalData(null);
        }}
        benefit={selectedModalData}
      />
    </section>
  );
};

interface Props extends ComponentProps<typeof Dialog> {
  benefit: MentoringBenefit | null;
}
const BenefitDialog = ({ benefit, ...rest }: Props) => {
  const styles = useStyles();

  return (
    <Dialog {...rest} className={styles.dialogRoot}>
      <img src={benefit?.image} alt="" className={styles.dialogImage} />
      <DialogTitle className={styles.dialogTitle}>{benefit?.title}</DialogTitle>
      <DialogContent>
        <DialogContentText
          className={styles.dialogText}
          dangerouslySetInnerHTML={{ __html: benefit?.description ?? '' }}
        />
      </DialogContent>
    </Dialog>
  );
};

export default BenefitSection;
